.day{
    background-color: #34495E;
    font-size: 39px;
    color: #ffffff;
    border-radius: 20px 0 0 20px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.task{
    background-color: #ECF0F1;
    width: 100%;
    border-radius: 0 20px 20px 0;
    display: flex;
    gap: 20px;
    align-items: center;
    padding: 10px 30px;
    flex-wrap: wrap;
}

.dayaList{
    display: flex;
    width: 100%;
    margin-bottom: 23px;
    position: relative;
    cursor: pointer;
}